import kotlin.js.Json

value class PoolMetadata(private val translations: List<Translation>) {
    fun randomTranslation() : Translation = translations.random()

    companion object {
        fun fromJson(json: Json) : PoolMetadata =
            (json["translations"] as? Array<*>)
                ?.mapNotNull {
                    val d = it.asDynamic()
                    val raw = d.raw as? String ?: return@mapNotNull null
                    val demo = d.demo as? String ?: return@mapNotNull null
                    val metadata = d.metadata ?: return@mapNotNull null
                    val theMap : Map<String, String> = hashMapOf<String, String>().also { map ->
                        (metadata.iterator() as Iterator<String>)
                            .asSequence()
                            .forEach { k ->
                                map[k] = metadata[k] as? String ?: return@mapNotNull null
                            }
                    }
                    Translation(raw, demo, theMap)
                }?.let(::PoolMetadata) ?: PoolMetadata(emptyList())
    }
}

data class Translation(val raw: String, val demo: String, val metadata: Map<String, String>) {
    val author: String? get() = metadata["author"]
}
