import org.w3c.files.Blob
import kotlin.js.Json
import kotlin.js.Promise

@JsNonModule
@JsModule("jszip")
external class JSZip {
    fun file(name: String, data: Any, options: Any? = definedExternally)
    fun generateAsync(options: Any?, onUpdate: (Json) -> Unit = definedExternally) : Promise<*>
}

private const val FILENAME = "assets/end_poem_extension/texts/end_poem/zh_cn.json"

fun constructTheZip(translationUrl: String, zipConfigUrl: String) {
    val jsZip = JSZip()
    fetchString(zipConfigUrl) { s ->
        val zipMeta = ZipMeta.fromJson(jsonObject(s))
        zipMeta.map.forEach { e ->
            e.value.writeToZip(jsZip, e.key)
        }
    }
    // translation
    UrlRefEntry(translationUrl).writeToZip(jsZip, FILENAME)

    val promise = jsZip.generateAsync(
        js(
            "{type:\"blob\",compression:\"DEFLATE\",compressionOptions:" +
                    "{level:9}}"
        )
    ).unsafeCast<Promise<Blob>>()
    promise.then(::downloadBlob)
}
