import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.dom.createElement
import org.khronos.webgl.Uint8Array
import org.w3c.dom.Element
import org.w3c.dom.HTMLAnchorElement
import org.w3c.dom.url.URL
import org.w3c.dom.url.URLSearchParams
import org.w3c.files.Blob
import kotlin.js.Json

var metaUrl: String? = null
lateinit var metaRoot: String
var zipConfigUrl: String? = null
lateinit var downloadPattern: String

fun main() {
    val query = searchMe()

    metaUrl = query.get("metadata")?.run(::decodeURIComponent)
    metaRoot = query.get("root")?.run(::decodeURIComponent) ?: "."
        .run { URL(this, window.location.href).toString() }
    zipConfigUrl = query.get("zipConfig")?.run(::decodeURIComponent)
    downloadPattern = query.get("dlPattern")?.run(::decodeURIComponent)
        ?: "end-poem-extpack-{}.zip"

    element("b_randomRoll")?.addEventListener("click", { randomRoll() })
}

fun randomRoll() {
    val metaUrl = checkNotNull(metaUrl)
    val zipConfigUrl = checkNotNull(zipConfigUrl)
    console.log("Start randomRoll()")

    fetchString(metaUrl) {
        console.log("Fetched ", metaUrl)
        val translation = PoolMetadata.fromJson(jsonObject(it)).randomTranslation()
        console.log("Got translation: ", translation.raw)
        constructTheZip(URL(translation.raw, metaRoot).toString(), zipConfigUrl)
        console.log("Constructing zip...")
    }
}

private fun element(id: String) : Element? = document.getElementById(id)
private fun searchMe() : URLSearchParams = window.location.search.let(::URLSearchParams)

