external fun decodeURIComponent(s: String) : String

fun jsonObject(s: String) : Json = JSON.parse(s)

fun downloadBlob(blob: Blob) {
    val filename = downloadPattern.replaceFirst("{}", randomHex(6))
    saveAs(blob, filename)
}

private fun randomHex(len: Int) : String =
    CharArray(len) { "0123456789abcdef".random() }.concatToString()

@JsNonModule
@JsModule("file-saver")
external fun saveAs(blob: Blob, filename: String)
