import kotlin.js.Json

value class ZipMeta(val map: Map<String, ZipEntry>) {
    companion object {
        fun fromJson(json: Json) : ZipMeta {
            val m : MutableMap<String, ZipEntry> = hashMapOf()
            (json.asDynamic().iterator() as Iterator<String>).forEach { k ->
                val b = json[k].asDynamic()
                if (b.from is String) {
                    m[k] = UrlRefEntry(b.from as String)
                } else if (b.raw is String) {
                    m[k] = RawEntry(b.raw as String)
                } else if (b.base64 is String) {
                    m[k] = Base64Entry(b.base64 as String)
                }
            }
            return ZipMeta(m)
        }
    }
}

sealed interface ZipEntry {
    fun writeToZip(zip: JSZip, entry: String)
}

class UrlRefEntry(private val url: String) : ZipEntry {
    override fun writeToZip(zip: JSZip, entry: String) {
        fetch(this.url) {
            zip.file(entry, it)
        }
    }
}

class RawEntry(private val s: String) : ZipEntry {
    override fun writeToZip(zip: JSZip, entry: String) {
        zip.file(entry, s)
    }
}

class Base64Entry(private val s: String) : ZipEntry {
    override fun writeToZip(zip: JSZip, entry: String) {
        zip.file(entry, Base64.toUint8Array(s))
    }
}
