import org.w3c.files.Blob
import org.w3c.xhr.BLOB
import org.w3c.xhr.TEXT
import org.w3c.xhr.XMLHttpRequest
import org.w3c.xhr.XMLHttpRequestResponseType

private const val OK : Short = 200

fun fetch(url: String, callback: (Blob) -> Unit) {
    val request = XMLHttpRequest()
    request.responseType = XMLHttpRequestResponseType.BLOB
    request.onreadystatechange = {
        if (request.readyState == XMLHttpRequest.DONE) {
            if (request.status == OK) {
                (request.response as Blob).run(callback)
            }
        }
    }
    request.open("POST", url)
    request.send()
}

fun fetchString(url: String, callback: (String) -> Unit) {
    val request = XMLHttpRequest()
    request.responseType = XMLHttpRequestResponseType.TEXT
    request.onreadystatechange = {
        if (request.readyState == XMLHttpRequest.DONE) {
            if (request.status == OK) {
                (request.response as String).run(callback)
            }
        }
    }
    request.open("GET", url)
    request.send()
}